import {
  TextField,
  Box,
  Button,
  Backdrop,
  CircularProgress,
  FormControlLabel,
  FormControl,
  RadioGroup,
  Radio,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import CancelIcon from "@mui/icons-material/Cancel";
import "./form.css";
import { addData, deleteImage, updateData, uploadImages } from "../../../API/AllRequestResponse";
import Tags from "../RComponents/Tags";
import AvailableService from "../RComponents/AvailableService";
import { useLocation } from "react-router";
import { FormLabel } from "react-bootstrap";
const FormFile = (props) => {
  const location = useLocation();
  const [form, setForm] = useState({
    title: "",
    location: "",
    price_range: "",
    area: "",
    tags: [],
    services: [],
    description: "",
    map: "",
    video: "",
    images: [],
    type: ""
  });
  const [tags, setTags] = useState("");
  const [services, setServices] = useState("");
  const [images, setImages] = useState([]);
  const [preViewImage, setPreViewImage] = useState([]);
  const [loading, setLoading] = useState(false);
  const [editFlag, setEditFlag] = useState(false);

  useEffect(() => {
    if (location.pathname === "/edit") {
      setForm(location.state.EditForm);
      setEditFlag(true);
    }
  },[location]);


  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm({ ...form, [name]: value });
  };

  const handleTags = (e) => {
    const { value } = e.target;
    setTags(value);
  };

  const handleServices = (e) => {
    const { value } = e.target;
    setServices(value);
  };

  const handleImages = (e) => {
    const { files } = e.target;
    setImages(files);
    if (files.length > 0) {
      let array = [];
      for (let i = 0; i < files.length; i++) {
        array.push(URL.createObjectURL(files[i]));
      }
      setPreViewImage(array);
    }
  };

  const handleDeleteImage = (index) => {
    let filesArray = [...images];
    setImages(filesArray.filter((item, i) => i !== index));
    let preViewArray = [...preViewImage];
    setPreViewImage(preViewArray.filter((item, i) => i !== index));
  };

  const handleTagKeyPress = (e) => {
    if (e.key === "Enter") {
      setForm({ ...form, tags: [...form.tags, tags] });
      setTags("");
    }
  };

  const handleServiceKeyPress = (e) => {
    if (e.key === "Enter") {
      setForm({ ...form, services: [...form.services, services] });
      setServices("");
    }
  };

  const handleDeleteTagIcon = (index) => {
    const newTags = form.tags.filter((tag, i) => i !== index);
    setForm({ ...form, tags: newTags });
  };

  const handleDeleteServiceIcon = (index) => {
    const newServices = form.services.filter((service, i) => i !== index);
    setForm({ ...form, services: newServices });
  };

  const handleSubmit = async () => {
    setLoading(true);
    try {
      let imagesArray = await handleUploadImages(images);
      let data = { ...form, images: imagesArray };
      await addData(data)
        .then((res) => {
          if (res.status) {
            setLoading(false);
            alert(res.message);
            window.location.href = "/admin";
          }
        })
        .catch((err) => {
          alert(err);
        });
    } catch (error) {
      alert("error", error);
    }
  };

  const handleUploadImages = async (images) => {
    const formImages = new FormData();
    
    for (let i = 0; i < images.length; i++) {
      formImages.append("images", images[i]);
    }
    let data = [];
    await uploadImages(formImages)
      .then((res) => {
        data = res.fileUrls;
      })
      .catch((err) => {
        alert(err);
      });
    return data;
  };


  const handleCloudImage = (image) => {
    setLoading(true);
    const imageName = image.name;
    const images = form.images;
    const newImages = images.filter((item) => item.name !== imageName);
    deleteImage(form.id,imageName)
      .then((res) => {
        setForm({ ...form, images: newImages });
        setLoading(false);
      })
      .catch((err) => {
        alert(err);
        setLoading(false);
      });
  }

  const handleUpdate = async () => {
    setLoading(true);
    try {
      let newImagesArray = await handleUploadImages(images);
      let imagesArray = [...form.images,...newImagesArray];
      let data = { ...form, images: imagesArray };
      await updateData(form.id,data)
        .then((res) => {
          if (res.status) {
            setLoading(false);
            alert(res.message);
            window.location.href = "/admin";
          }
        })
        .catch((err) => {
          alert(err);
        });
    } catch (error) {
      alert("error", error);
    }
  };

  return (
    <div className="form-box">
      <div className="inner-form-box">
        <div className="form-head">
          <h1>{editFlag ? `Edit` : `Add` } Form</h1>
        </div>
        <div className="form-body">
          <Box
            component="form"
            sx={{
              width: 700,
              maxWidth: "100%",
            }}
            noValidate={false}
            autoComplete="off"
          >
            <div className="form-input">
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Title"
                  variant="filled"
                  name="title"
                  value={form.title}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <FormControl component="fieldset">
                  <FormLabel component="legend">Type</FormLabel>
                  <RadioGroup
                    aria-label="type"
                    name="type"
                    value={form.type}
                    onChange={handleChange}
                  >
                    <FormControlLabel
                      value="flat"
                      control={<Radio />}
                      label="Flat"
                      disabled={editFlag}
                    />
                    <FormControlLabel
                      value="plot"
                      control={<Radio />}
                      label="Plot"
                      disabled={editFlag}
                    />
                    <FormControlLabel
                      value="new"
                      control={<Radio />}
                      label="Own Venture"
                      disabled={editFlag}
                    />
                  </RadioGroup>
                </FormControl>
              </div>
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Location"
                  variant="filled"
                  name="location"
                  value={form.location}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Price Range"
                  variant="filled"
                  name="price_range"
                  value={form.price_range}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Area"
                  variant="filled"
                  name="area"
                  value={form.area}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <TextField
                  fullWidth
                  id="filled-required"
                  label="Tags"
                  variant="filled"
                  name="tags"
                  value={tags}
                  onChange={handleTags}
                  onKeyPress={handleTagKeyPress}
                />
              </div>
              <div className="form-tags">
                {form.tags.map((tag, index) => (
                  <div key={index} className="tags">
                    <Tags
                      key={index}
                      Title={tag}
                      enableDelete={true}
                      handleDelete={handleDeleteTagIcon}
                      tag_id={index}
                    />
                  </div>
                ))}
              </div>
              <div className="form-inputs">
                <TextField
                  fullWidth
                  id="filled-required"
                  label="Services"
                  variant="filled"
                  name="services"
                  value={services}
                  onChange={handleServices}
                  onKeyPress={handleServiceKeyPress}
                />
              </div>
              <div className="form-tags">
                {form.services.map((service, index) => (
                  <div key={index} className="tags">
                    <AvailableService
                      key={index}
                      service={service}
                      correct={service}
                      enableDelete={true}
                      handleDelete={handleDeleteServiceIcon}
                      service_id={index}
                    />
                  </div>
                ))}
              </div>
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Description"
                  variant="filled"
                  name="description"
                  value={form.description}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Map"
                  variant="filled"
                  name="map"
                  value={form.map}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <TextField
                  required
                  fullWidth
                  id="filled-required"
                  label="Video"
                  variant="filled"
                  name="video"
                  value={form.video}
                  onChange={handleChange}
                />
              </div>
              <div className="form-inputs">
                <Button variant="contained" component="label">
                  Upload File
                  <input type="file" hidden multiple onChange={handleImages} />
                </Button>
                {
                  <div className="preview-image">
                    {images.length > 0 ? (
                      preViewImage.map((image, index) => {
                        return (
                          <div key={index} className="image-box">
                            <img
                              src={image}
                              alt="preview"
                              className="preview"
                            />
                            <div>
                              <CancelIcon
                                color="error"
                                fontSize="medium"
                                className="image-del"
                                onClick={() => handleDeleteImage(index)}
                              />
                            </div>
                          </div>
                        );
                      })
                    ) : (
                      <div className="no-image">No New Image</div>
                    )}
                  </div>
                }
                {editFlag && (
                  <div className="preview-image">
                    {form.images.length > 0 ? (
                      form.images.map((image, index) => {
                        return (
                          <div key={index} className="image-box">
                            <img
                              src={image.url}
                              alt={image.name}
                              className="preview"
                            />
                            <div>
                              <CancelIcon
                                color="primary"
                                fontSize="medium"
                                className="image-del"
                                onClick={() => handleCloudImage(image)}
                              />
                            </div>
                          </div>
                        );
                      })
                    ) : (
                      <div className="no-image">No Cloud Image</div>
                    )}
                  </div>
                )}
              </div>
              {!editFlag && (
                <div className="form-inputs ">
                  <Button
                    variant="contained"
                    color="success"
                    className="submit"
                    onClick={handleSubmit}
                  >
                    Submit
                  </Button>
                </div>
              )}
              {editFlag && (
                <div className="form-inputs ">
                  <Button
                    variant="contained"
                    color="primary"
                    className="submit"
                    onClick={handleUpdate}
                  >
                    Update
                  </Button>
                </div>
              )}
            </div>
          </Box>
        </div>
        <div>
          <Backdrop
            sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={loading}
          >
            <CircularProgress color="inherit" />
          </Backdrop>
        </div>
      </div>
    </div>
  );
};

export default FormFile;
